"use client";

import { useEffect, useState } from "react";
import { Copy, LinkIcon, Pointer, RefreshCw } from "lucide-react";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { AffiliateAPI } from "@/utils/api";
import { useToast } from "@/components/ui/toast";
type Dashboard = {
    referrals_count: number;
    amount: number;
    percent: number;
    year: number;
    month: number;
    tenants: string[];
    payouts: { pending_total: number; paid_total: number };
};

type LinkItem = { token: string; url: string; active: boolean };

export default function GenerateLinkPage() {
    const { show } = useToast();
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [dash, setDash] = useState<Dashboard | null>(null);
    const [payoutMonth, setPayoutMonth] = useState("");
    const [payoutPercent, setPayoutPercent] = useState<number>(5);
    const [links, setLinks] = useState<LinkItem[]>([]);
    const [canCreateMore, setCanCreateMore] = useState(true);

    const createLinkLabel = canCreateMore ? "Generate referral link" : "Link limit reached";

    async function onCreateLink() {
        try {
            await AffiliateAPI.createLink();
            await refreshAll();
            show({ variant: "success", title: "Link created" });
        } catch (e: any) {
            show({ variant: "destructive", title: "Failed to create link", description: e.message });
        }
    }
    async function copyLink(url: string) {
        try {
            if (typeof navigator !== "undefined" && navigator.clipboard?.writeText) {
                await navigator.clipboard.writeText(url);
                show({ variant: "success", title: "Link copied", description: "Referral link copied to clipboard." });
            } else {
                throw new Error("Clipboard not available");
            }
        } catch (e: any) {
            show({
                variant: "destructive",
                title: "Copy failed",
                description: e?.message || "Unable to copy link on this device",
            });
        }
    }
    async function onDeactivate(token: string) {
        try {
            await AffiliateAPI.deactivate(token);
            await refreshAll();
            show({ variant: "success", title: "Link deactivated" });
        } catch (e: any) {
            show({ variant: "destructive", title: "Failed to deactivate", description: e.message });
        }
    }
    async function refreshAll() {
        setLoading(true);
        try {
            const [d, l] = await Promise.all([
                AffiliateAPI.dashboard(),
                AffiliateAPI.getLinks(),
            ]);
            const dashData = d as Dashboard;
            setDash(dashData);
            if (dashData?.year && dashData?.month) {
                const monthStr = `${dashData.year}-${String(dashData.month).padStart(2, "0")}`;
                setPayoutMonth(monthStr);
            }
            if (dashData?.percent) {
                setPayoutPercent(dashData.percent);
            }
            setLinks((l as any).links || []);
            setCanCreateMore(Boolean((l as any).can_create_more));
            setError(null);
        } catch (e: any) {
            setError(e.message || "Failed to load affiliate data");
            show({ variant: "destructive", title: "Error", description: e.message || "Failed to load" });
        } finally {
            setLoading(false);
        }
    }

    async function onRotate(token: string) {
        try {
            await AffiliateAPI.rotate(token);
            await refreshAll();
            show({ variant: "success", title: "Link rotated" });
        } catch (e: any) {
            show({ variant: "destructive", title: "Failed to rotate", description: e.message });
        }
    }
    useEffect(() => {
        refreshAll();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if (loading) {
        return (
            <Card className="border-emerald-100/60 bg-white shadow-sm">
                <CardHeader>
                    <CardTitle className="text-base font-semibold text-emerald-800">Fetching affiliate links</CardTitle>
                    <CardDescription className="text-sm text-slate-500">Please hold on while we load your referral details.</CardDescription>
                </CardHeader>
                <CardContent className="text-sm text-slate-500">Loading…</CardContent>
            </Card>
        );
    }

    if (error) {
        return (
            <Card className="border-rose-200 bg-rose-50/80 shadow-sm">
                <CardHeader>
                    <CardTitle className="text-base font-semibold text-rose-700">We couldn’t load your links</CardTitle>
                    <CardDescription className="text-sm text-rose-500">{error}</CardDescription>
                </CardHeader>
                <CardContent>
                    <Button onClick={refreshAll} variant="outline" className="rounded-xl border-rose-300 text-rose-600 hover:bg-rose-100">
                        <RefreshCw className="mr-2 h-4 w-4" /> Try again
                    </Button>
                </CardContent>
            </Card>
        );
    }

    return (
        <div className="space-y-8">
            <Card className="border-emerald-100 bg-white shadow-[0_25px_70px_-50px_rgba(16,185,129,0.45)]">
                <CardHeader className="space-y-3">
                    <span className="inline-flex items-center gap-2 rounded-full border border-emerald-200 bg-emerald-50 px-3 py-1 text-[11px] font-semibold uppercase tracking-[0.32em] text-emerald-600">
                        Affiliate tools
                    </span>
                    <CardTitle className="text-2xl font-semibold text-slate-900">Share links with the same polish as your landing page.</CardTitle>
                    <CardDescription className="text-sm text-slate-600">
                        Manage referral URLs, rotate tokens, and keep your partners moving without leaving this clean control panel.
                    </CardDescription>
                </CardHeader>
                <CardContent className="flex flex-col gap-3 sm:flex-row sm:items-center">
                    <Button
                        onClick={onCreateLink}
                        disabled={!canCreateMore}
                        className="inline-flex items-center gap-2 rounded-xl bg-emerald-600 px-5 py-3 text-sm font-semibold text-white shadow-sm transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:opacity-60"
                    >
                        <span className="flex h-8 w-8 items-center justify-center rounded-lg bg-white/15 text-white">
                            <Pointer className="h-4 w-4" />
                        </span>
                        <span>{createLinkLabel}</span>
                    </Button>
                    <Button
                        onClick={refreshAll}
                        variant="outline"
                        className="inline-flex items-center gap-2 rounded-xl border-emerald-200 px-5 py-3 text-sm font-semibold text-emerald-700 transition hover:border-emerald-300 hover:bg-emerald-50"
                    >
                        <RefreshCw className="h-4 w-4" /> Refresh data
                    </Button>
                    <p className="text-xs text-slate-500">
                        {links.length} of 2 active links in use · {canCreateMore ? "Generate a new link to start sharing" : "Deactivate one to free a slot"}
                    </p>
                </CardContent>
            </Card>

            <Card className="border-slate-200 bg-white shadow-sm">
                <CardHeader>
                    <CardTitle className="text-lg font-semibold text-slate-900">Your referral links</CardTitle>
                    <CardDescription className="text-sm text-slate-500">Copy, deactivate, or rotate tokens whenever you need.</CardDescription>
                </CardHeader>
                <CardContent>
                    {links.length === 0 ? (
                        <div className="flex flex-col items-center justify-center gap-3 rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/60 p-8 text-center text-sm text-emerald-600">
                            <LinkIcon className="h-8 w-8 text-emerald-500" />
                            <p>No referral links yet. Generate one to start sharing.</p>
                        </div>
                    ) : (
                        <div className="space-y-3">
                            {links.map((link) => {
                                const statusClass = link.active
                                    ? "bg-emerald-100 text-emerald-700"
                                    : "bg-slate-100 text-slate-600";
                                return (
                                    <div
                                        key={link.token}
                                        className="flex flex-col gap-4 rounded-2xl border border-slate-200 bg-white p-5 shadow-sm sm:flex-row sm:items-center sm:justify-between"
                                    >
                                        <div className="space-y-2">
                                            <p className="flex items-center gap-2 text-sm font-semibold text-slate-900">
                                                <LinkIcon className="h-4 w-4 text-emerald-600" /> Referral link
                                                <span className={`inline-flex items-center rounded-full px-2 py-0.5 text-[11px] font-semibold uppercase tracking-[0.28em] ${statusClass}`}>
                                                    {link.active ? "Active" : "Inactive"}
                                                </span>
                                            </p>
                                            <p className="font-mono text-xs text-slate-600 break-all">{link.url}</p>
                                        </div>
                                        <div className="flex flex-wrap items-center gap-2">
                                            <Button
                                                size="sm"
                                                variant="outline"
                                                onClick={() => copyLink(link.url)}
                                                className="rounded-xl border-emerald-200 text-emerald-700 hover:border-emerald-300 hover:bg-emerald-50"
                                            >
                                                <Copy className="mr-1.5 h-4 w-4" /> Copy
                                            </Button>
                                            <Button
                                                size="sm"
                                                variant="outline"
                                                onClick={() => onDeactivate(link.token)}
                                                disabled={!link.active}
                                                className="rounded-xl border-rose-200 text-rose-600 hover:border-rose-300 hover:bg-rose-50 disabled:cursor-not-allowed disabled:opacity-60"
                                            >
                                                Deactivate
                                            </Button>
                                            <Button
                                                size="sm"
                                                variant="outline"
                                                onClick={() => onRotate(link.token)}
                                                className="rounded-xl border-slate-200 text-slate-600 hover:border-slate-300 hover:bg-slate-50"
                                            >
                                                <RefreshCw className="mr-1.5 h-4 w-4" /> Rotate
                                            </Button>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    )}
                </CardContent>
            </Card>
        </div>
    );
}